@font-face {
  font-family: "Lato";
  src: url("../../fonts/lato-regular.woff2") format("woff2"), url("../../fonts/lato-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: calc(100% - 40px);
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
    width: calc(100% - 60px);
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
    width: 1220px;
  }
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0 auto;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Lato", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-style: normal;
  word-break: break-word;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
    color: #000000;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.logo--mobile {
  display: block;
  max-width: 256px;
  min-height: 38px;
}
@media (min-width: 768px) {
  .logo--mobile {
    display: none;
  }
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}